.dropmenu-custom {
  position: relative;
}
.dropmenu-custom__head {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  font-size: 16px;
  font-weight: 325;
  line-height: 16px;
  padding: 6px 0 0;
}
.dropmenu-custom__head *:hover {
  cursor: pointer;
}
.dropmenu-custom__head > svg {
  width: 20px;
  height: 20px;
}
.dropmenu-custom__head.active {
  background-color: #ebebeb;
}
.dropmenu-custom__body {
  position: absolute;
}

.navigation-sub-menu {
  background-color: #fff;
  text-decoration: none;
  color: #414042;
  display: flex;
}
.navigation-sub-menu:hover {
  text-decoration: none;
  background-color: #f2f7f9;
}
.navigation-sub-menu:hover .navigation-sub-menu__media-wrapper img {
  transform: scale(1.2);
}
.navigation-sub-menu:active {
  background-color: #c6d8e2;
}
.navigation-sub-menu__details {
  padding-top: 1rem;
  padding-right: 1rem;
  padding-bottom: 1rem;
  padding-left: 1rem;
  width: 100%;
  min-width: 225px;
  transition: padding 0.3s cubic-bezier(0.17, 0.67, 0.27, 1);
}
.navigation-sub-menu__draw-info {
  display: flex;
}
.navigation-sub-menu__draw-info .navigation-sub-menu__notice {
  display: flex;
  align-items: center;
}
.navigation-sub-menu__draw-info .navigation-sub-menu__notice img {
  max-height: 16px;
  max-width: 16px;
  margin-right: 4px;
}
.navigation-sub-menu__draw-info .navigation-sub-menu__notice span {
  font-size: 0.625rem;
  line-height: 15px;
  text-transform: uppercase;
  font-weight: 400;
}
.navigation-sub-menu__draw-info .navigation-sub-menu__notice.notice-win img {
  filter: brightness(0) saturate(100%) invert(34%) sepia(18%) saturate(4548%) hue-rotate(134deg) brightness(100%) contrast(101%);
}
.navigation-sub-menu__draw-info .navigation-sub-menu__notice.notice-win span {
  color: #008666;
}
.navigation-sub-menu__draw-info .navigation-sub-menu__notice.notice-info img {
  filter: brightness(0) saturate(100%) invert(32%) sepia(100%) saturate(1063%) hue-rotate(172deg) brightness(94%) contrast(111%);
}
.navigation-sub-menu__draw-info .navigation-sub-menu__notice.notice-info span {
  color: #0081c8;
}
.navigation-sub-menu__draw-info .navigation-sub-menu__notice.notice-alert img {
  filter: brightness(0) saturate(100%) invert(29%) sepia(83%) saturate(2440%) hue-rotate(335deg) brightness(78%) contrast(105%);
}
.navigation-sub-menu__draw-info .navigation-sub-menu__notice.notice-alert span {
  color: #ca2535;
}
.navigation-sub-menu__draw-info .badge-container.high-priority.badge-default {
  background-color: #ffd51c;
  margin-bottom: 0;
}
.navigation-sub-menu__draw-info .badge-container .label-text {
  line-height: 0.8125rem;
  color: #005b8d;
}
.navigation-sub-menu__heading {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.navigation-sub-menu__heading p {
  margin-top: 0;
  margin-bottom: 0;
  justify-self: end;
  font-size: 1rem;
  line-height: 150%;
}
.navigation-sub-menu__heading img {
  max-height: 24px;
  max-width: 36px;
  margin-left: auto;
  transition: all 0.3s cubic-bezier(0.17, 0.67, 0.27, 1);
}
.navigation-sub-menu__media-wrapper {
  position: relative;
  max-width: 75px;
  min-height: 105px;
  width: 100%;
  overflow: hidden;
}
.navigation-sub-menu__media-wrapper .navigation-sub-menu__image-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
}
.navigation-sub-menu__media-wrapper .navigation-sub-menu__image-wrapper img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .navigation-sub-menu__details {
    padding-left: 1rem;
  }
}

button.navigation-sub-menu {
  border: none;
  padding: 0;
  cursor: pointer;
}
button.navigation-sub-menu.open .navigation-sub-menu__heading img {
  transform: rotate(180deg);
  transition: all 0.3s cubic-bezier(0.17, 0.67, 0.27, 1);
}

ul.active .navigation-sub-menu__details {
  padding-left: 2rem;
  transition: padding 0.3s 0.3s cubic-bezier(0.17, 0.67, 0.27, 1);
}

.collapse-custom {
  width: 300px;
  background-color: white;
}
.collapse-custom.dropdown {
  display: flex;
}
.collapse-custom.dropdown .item {
  margin-right: 5px;
  position: relative;
}
.collapse-custom.dropdown .collapse-custom__body {
  position: absolute;
  transform: scale(1, 1) translateX(-50%) translateY(0%);
  opacity: 0;
  left: 50%;
  width: 300px;
  top: calc(100% + 23px);
  transform-origin: top center;
  z-index: 1002;
}
.collapse-custom.dropdown .collapse-custom__body.t1 {
  transform: scale(1, 1) translateX(-50%) translateY(0%);
  opacity: 1;
  transform-origin: top center;
  transition-duration: 0.15s;
}

.collapse-custom__head {
  width: 100%;
  padding: 1rem;
  color: #4d4d4d;
  background-color: #fafafa;
  border: 1px solid #ccc;
  text-align: left;
  white-space: nowrap;
}
.collapse-custom__head:hover {
  cursor: pointer;
  background-color: #ebebeb;
}

.collapse-custom__body {
  display: block;
  transition: max-height 0.3s cubic-bezier(0.17, 0.67, 0.27, 1), transform 0.3s cubic-bezier(0.17, 0.67, 0.27, 1), opacity 0.3s cubic-bezier(0.17, 0.67, 0.27, 1);
  overflow: hidden;
  transform: scale(1, 1);
  opacity: 1;
  padding: 0;
  max-height: 0;
  overflow: hidden;
}
.collapse-custom__body a {
  display: block;
  padding: 1rem;
  border-bottom: 1px solid #ccc;
  color: #4d4d4d;
  text-decoration: none;
}
.collapse-custom__body a:hover {
  text-decoration: underline;
  background-color: #ebebeb;
}
.collapse-custom__body a:last-child {
  border-bottom: none;
}
.collapse-custom__body > div {
  background-color: #fff;
  border: 1px solid #ccc;
}
.collapse-custom__body.collapsed {
  display: none;
}

.navigation__login > button {
  cursor: pointer;
}

.desktop-login {
  display: none;
}

.login-cart-wrapper-outer {
  display: block;
}

ul.navigation__ul.desktop.collapse-custom.dropdown {
  display: none;
}

@media (min-width: 1024px) {
  ul.navigation__ul.desktop.collapse-custom.dropdown {
    display: flex;
    width: 100%;
  }
  .collapse-custom.dropdown {
    display: flex;
  }
  .navigation__login {
    display: none;
  }
  .desktop-login {
    display: block;
  }
  li.navigation__login.status--logged-in.item {
    display: none;
  }
