ex;\n    flex-wrap: wrap;\n    flex-direction: row;\n\n    &.nested-row {\n        // @include breakpoint(xs) {\n        //   margin-right: -0.5rem;\n        //   margin-left: -0.5rem;\n        // }\n\n        @include breakpoint(sm2) {\n            margin-right: -1rem;\n            margin-left: -1rem;\n        }\n    }\n}\n\n.row {\n    &.reverse {\n        flex-direction: row-reverse;\n    }\n\n    &.reverse-md {\n        @include breakpoint(md) {\n            flex-direction: row-reverse;\n        }\n    }\n}\n\n.col.reverse {\n    flex-direction: column-reverse;\n}\n\n$breakpoints: xs 320px 100%, sm 375px 100%, sm2 480px 100%, md 768px 100%,\n    md2 1024px 100%, lg 1140px 100%, lg2 1280px 100%, xl 1600px 100%;\n\n$display-values: none, inline, inline-block, block, grid, inline-grid, table,\n    table-cell, table-row, flex, inline-flex;\n\n@mixin col-gutter {\n    box-sizing: border-box;\n    flex-grow: 0;\n    flex-shrink: 0;\n    padding-right: 0;\n    padding-left: 0;\n\n    @include breakpoint(sm2) {\n        padding-right: 1rem;\n        padding-left: 1rem;\n    }\n}\n\n@each $breakpoint in $breakpoints {\n    $name: nth($breakpoint, 1);\n    $size: nth($breakpoint, 2);\n    $container: nth($breakpoint, 3);\n\n    @media only screen and (min-width: $size) {\n        .container {\n            width: $container;\n        }\n\n        .col-#{$name} {\n            @include col-gutter;\n\n            flex-grow: 1;\n            flex-basis: 0;\n            max-width: 100%;\n        }\n\n        @for $i from 1 through 12 {\n            .col-#{$name}-#{$i} {\n                @include col-gutter;\n\n                flex-basis: 100%/12 * $i;\n                max-width: 100%/12 * $i;\n                margin-bottom: 1rem;\n\n                @include breakpoint(md2) {\n                    margin-bottom: 2rem;\n                }\n\n                &.no-gutter {\n                    padding-right: 0;\n                }\n\n                &.m0-bottom {\n                    margin-bottom: 0;\n                }\n            }\n        }\n\n        @for $i from 0 through 12 {\n            .col-#{$name}-offset-#{$i} {\n                @include col-gutter;\n\n                @if $i==0 {\n                    margin-left: 0;\n                } @else {\n                    margin-left: 100%/12 * $i;\n                }\n            }\n        }\n\n        .start-#{$name} {\n            justify-content: flex-start;\n            text-align: left;\n        }\n\n        .center-#{$name} {\n            justify-content: center;\n            text-align: center;\n        }\n\n        .end-#{$name} {\n            justify-content: flex-end;\n            text-align: right;\n        }\n\n        .top-#{$name} {\n            align-items: flex-start;\n        }\n\n        .middle-#{$name} {\n            align-items: center;\n        }\n\n        .bottom-#{$name} {\n            align-items: flex-end;\n        }\n\n        .around-#{$name} {\n            justify-content: space-around;\n        }\n\n        .between-#{$name} {\n            justify-content: space-between;\n        }\n\n        .first-#{$name} {\n            order: -1;\n        }\n\n        .last-#{$name} {\n            order: 1;\n        }\n\n        @each $display-value in $display-values {\n            .d-#{$name}-#{$display-value} {\n                display: $display-value;\n            }\n        }\n    }\n}\n\n// For 1 to 12 column layout styles default styles if there is no size has mentioned in the class name\n@for $i from 1 through 12 {\n    $value: $i * 1%;\n\n    .col-#{$i} {\n        max-width: (100/12) * $value;\n        flex: 0 0 (100/12) * $value;\n        padding-right: 1rem;\n\n        &.no-gutter {\n            padding-right: 0;\n        }\n\n        // For columns inside another column\n        @for $childColumn from 1 through $i {\n            $childColumnValue: $childColumn * 1%;\n\n            .col-#{$childColumn} {\n                max-width: (100 / $i) * $childColumnValue;\n                flex: 0 0 (100 / $i) * $childColumnValue;\n                padding-right: 1rem;\n\n                @include breakpoint(sm2) {\n                    padding-right: 2rem;\n                }\n            }\n        }\n\n        @include breakpoint(sm2) {\n            padding-right: 2rem;\n        }\n    }\n}\n\n.justify-center {\n    justify-content: center;\n\n    &-md2 {\n        @include breakpoint(md2) {\n            justify-content: center;\n        }\n    }\n}\n\n.justify-start {\n    justify-content: start;\n}\n\n.justify-end {\n    justify-content: flex-end;\n}\n\n.justify-around {\n    justify-content: space-around;\n}\n\n.justify-between {\n    justify-content: space-between;\n}\n\n.center {\n    text-align: center;\n}\n\n.left {\n    text-align: left;\n}\n\n.right {\n    text-align: right;\n}\n\n.align-start {\n    align-items: flex-start;\n}\n\n.align-end {\n    align-items: flex-end;\n}\n\n.align-center {\n    align-items: center;\n}\n","// Breakpoints to write media queries\n\n@mixin breakpoint($point) {\n    @if $point==xl {\n        @media (min-width: 1600px) {\n            @content;\n        }\n    } @else if $point==lg2 {\n        @media (min-width: 1280px) {\n            @content;\n        }\n    } @else if $point==lg {\n        @media (min-width: 1140px) {\n            @content;\n        }\n    } @else if $point==md2 {\n        @media (min-width: 1024px) {\n            @content;\n        }\n    } @else if $point==md {\n        @media (min-width: 768px) {\n            @content;\n        }\n    } @else if $point==sm2 {\n        @media (min-width: 480px) {\n            @content;\n        }\n    } @else if $point==sm {\n        @media (min-width: 375px) {\n            @content;\n        }\n    } @else if $point==xs {\n        @media (min-width: 320px) {\n            @content;\n        }\n    }\n}\n","$col_base: #606060;\n$col_black: #000;\n$col_grey_30: #edebe9;\n$col_faded_grey: #a19f9d;\n$col_prim_hover: #bc2130;\n$col_prim_navy: #002b60;\n$col_prim_red: #ca2535;\n$col_prim_yellow: #fdb924;\n$col_prim_blue: #006fba;\n$col_prim_teal: #009dca;\n$col_prim_green: #6b7b4a;\n$col_prim_purple: #4b3166;\n$col_prim_orange: #ff7e2f;\n$col_accent_teal: #009fb8;\n$col_accent_purple: #4b3166;\n$col_accent_orange: #ff7e2f;\n$col_accent_green: #0b703d;\n$col_accent_grey: #696969;\n$col_yellow: #faaf30;\n$col_yellow_vis_vis: #ffeea4;\n$col_yellow_milk_punch: #fff7d2;\n$col_yellow_my_sin: #ffb31c;\n$col_winwin_blue: #114168;\n$col_secondary_grey: #605e5c;\n$col_light_grey: #d2d0ce;\n$col_paragraph: #3b3a39;\n$col_modal_grey: #414042;\n$col_white: #fff;\n$col_grey: #eee;\n$col_dark_grey: #959595;\n$col_darker_grey: #9ea9b6;\n$col_grey_90: #605e5c;\n$col_grey_background: #f4f4f4;\n$col_grey_border: #d3d3d3;\n$col_footer_text: #fff;\n$col_footer_hover: #99aabf;\n$col_light_yellow: #fff2df;\n$col_prim_red_hover: #e52535;\n$light_lilac_background: #ddd4e3;\n$col_teal_overlay: rgba(25, 178, 202, 0.8);\n$col_red_overlay: rgba(202, 37, 53, 0.8);\n$col_grey_text: rgba(0, 0, 0, 0.85);\n$col_lite_grey_text: rgba(0, 0, 0, 0.6);\n$col_blue: #034e71;\n$col_grey_granite: #575757;\n$col_RSL_primary: #0c2748;\n$col_base_purple: #552873;\n$col_spanish_green: #179447;\n$col_gold: #d5a664;\n$col_lite_red: #ffebed;\n$col_alice_blue: #edf5ff;\n$col_orange: #f4881f;\n$col_lighter_rajah: #f5c173;\n$col_silver: #c4c4c4;\n$col_cloud: #c8c6c4;\n$col_whiskey_yellow: #d5a563;\n$col_dixie_yellow: #e99516;\n$col_affair_purple: #77538f;\n$col_willow_brook_green: #dbe8d9;\n$palm_leaf_green: #13390d;\n$col_Botticelli: #dae4ef;\n$col_dell_green: #256a19;\n$col_black_squeeze: #eaeff7;\n$col_pampas_grey: #faf9f8;\n$col_anakiwa_blue: #85ceff;\n$col_dodger_blue: #0c9dff;\n$fuchsia_blue: #8a3dbd;\n$catskill_white: #f2f7f9;\n$rajah_yellow: #ffd51c;\n$col_botticelli_secondary_blue: #c6d8e2;\n$col_botticelli_blue: #d0d7e5;\n$col_mercury: #e3e3e3;\n$col_my_sin: #ffb31c;\n$col_milk_punch: #fff7d2;\n\n// Form variables\n$border-col: #ddd;\n$bg-col: #fafafa;\n$label-col: #666;\n$placeholder-col: #767676; //lightest possible grey that passes accessibility\n$input-bg-col: #fff;\n$input-text-col: $col_prim_navy;\n$text-col: #212121;\n$col-anchor: #2162ae;\n$col-border-grey: #e1dfdd;\n$password_strength_green: #2b811d;\n$password_strength_red: #d50000;\n$password_strength_line_green: #64dd17;\n$password_strength_line_yellow: #ffc400;\n$password_strength_line_grey: #e0e0e0;\n$password_strength_line_red: #ff1744;\n$blue_rajah: #005b8d;\n$rock_blue: #9caac5;\n$rock_blue_variation: #95b9cd;\n$col_chestnut_rose: #cf5449;\n$col_vanilla_ice: #f3d4d2;\n$lynch_blue: #64708e;\n$col_deep_sea: #008666;\n$col_well_read: #ba2f3d;\n$col_red_ribbon: #f4273b;\n$yellow_rajah: #f0f3f8;\n$col_lochmara: #0081c8;\n$pacific_blue: #0098c8;\n$white-pointer: #fcf2ff;\n$col_light_wisteria: #c38bd7;\n$col_french_lilac: #e0beeb;\n$col_astral: #327ba3;\n$hippie_blue: #639ab8;\n$col_amethyst: #a757c2;\n$milk_punch: #fff7d2;\n$my_sin: #ffb31c;\n$aero_blue: #d2ffe4;\n\n// 2023 RSL Lottery\n$purple_seance: #8a23ae;\n$trans_white: #fff9;\n\n// Box shadows\n$red_box_shadow: 0 4px 8px rgba(202, 37, 53, 0.25);\n$dark_blue_box_shadow: 0 4px 8px rgba(0, 91, 141, 0.25);\n$purple_dark_box-shadow: 0 4px 8px rgba(138, 35, 174, 0.25);\n$yellow_box_shadow: 0 4px 8px rgba(255, 213, 28, 0.25);\n$green_box_shadow: 0 4px 8px rgba(0, 134, 102, 0.25);\n$mono_dark_box_shadow: 0 4px 8px rgba(65, 64, 66, 0.25);\n\n// Linear gradients\n$red_gradient: linear-gradient(45deg, $col_well_read 0%, $col_red_ribbon 100%);\n$blue_dark_gradient: linear-gradient(\n    49deg,\n    #005b8d 20.83%,\n    #0081c8 79.69%,\n    #0098c8 100%\n);\n$purple_dark_gradient: linear-gradient(\n    47deg,\n    #552873 19.79%,\n    $fuchsia_blue 100%\n);\n$yellow_gradient: linear-gradient(47deg, #ffd51c 19.79%, #ffb31c 100%);\n$green_gradient: linear-gradient(47deg, #008666 19.79%, #0ea781 100%);\n// My membership\n$card_border: #d0d7e5;\n\nbody {\n    background-color: red;\n}\n\n.accordion-custom {\n    margin-bottom: 1rem;\n\n    &__item {\n        border: 1px solid $rock_blue_variation;\n        background-color: $col_white;\n        border-radius: 0;\n        overflow: hidden;\n        box-shadow: 0 4px 8px 0 rgba(50, 123, 163, 0.25);\n        border-top: none;\n\n        &:first-child {\n            border-radius: 8px 8px 0 0;\n            border-top: 1px solid $rock_blue_variation;\n        }\n\n        &:last-child {\n            border-radius: 0 0 8px 8px;\n        }\n\n        &:only-child {\n            border-radius: 8px !important;\n            border-top: 1px solid $rock_blue_variation;\n        }\n\n        &.accordion-item-active {\n            .accordion-custom {\n                &__head {\n                    border-bottom: none;\n\n                    &::after {\n                        width: calc(100% - 2rem);\n                        transition-duration: 0.25s;\n                        transition-delay: 0.5s;\n                    }\n                }\n            }\n        }\n    }\n\n    &__head {\n        padding: 1rem;\n    }\n\n    &__inner {\n        padding: 10px 15px;\n    }\n\n    &__head {\n        position: relative;\n        border-collapse: collapse;\n        box-sizing: border-box;\n        cursor: pointer;\n        transition: background-color 0.3s ease-in-out;\n\n        &::before {\n            transition: width 0.3s ease-in-out 0s;\n            width: 0;\n            content: \" \";\n            display: inline-block;\n            left: 1rem;\n            height: 1px;\n            border-top: 1px solid $rock_blue_variation;\n            position: absolute;\n            bottom: 0;\n        }\n\n        &::after {\n            content: \" \";\n            display: inline-block;\n            left: 1rem;\n            height: 1px;\n            // background-color: $rock_blue_variation;\n            border-top: 1px solid $rock_blue_variation;\n            position: absolute;\n            bottom: 0;\n            width: 0;\n        }\n\n        &.accordion-head-active {\n            border-bottom-left-radius: 0;\n            border-bottom-right-radius: 0;\n            border-bottom: none;\n\n            &::before {\n                width: calc(100% - 32px);\n                transition-delay: 0.3s;\n            }\n\n            .accordion-custom {\n                &__icon {\n                    img {\n                        transform: rotate(180deg);\n                    }\n                }\n            }\n        }\n    }\n\n    &__body {\n        border-bottom-left-radius: 8px;\n        border-bottom-right-radius: 8px;\n    }\n\n    &__title-wrapper {\n        display: flex;\n        width: 100%;\n        align-items: center;\n        justify-content: space-between;\n    }\n\n    &__title {\n        display: flex;\n        justify-content: flex-start;\n        align-items: center;\n        font-size: 1rem;\n        line-height: 150%;\n\n        strong {\n            font-family: \"Gotham bold\";\n        }\n    }\n\n    &__title-icon {\n        margin-right: 0.5rem;\n\n        img {\n            width: 24px;\n            height: 24px;\n        }\n    }\n\n    &__icon {\n        margin-left: 1rem;\n\n        img {\n            max-width: 24px;\n            max-height: 24px;\n        }\n    }\n\n    &__body {\n        max-height: 0;\n        overflow: hidden;\n        transition: overflow 0s linear 0.1s,\n            max-height 0.5s cubic-bezier(0.65, -0.45, 0.32, 1.33);\n\n        ul,\n        ol {\n            margin-top: 1rem;\n            margin-bottom: 1rem;\n        }\n\n        ul {\n            padding-left: 1rem;\n        }\n\n        ol {\n            padding-left: 1.3rem;\n        }\n\n        li {\n            margin-bottom: 1rem;\n        }\n\n        p,\n        li {\n            font-size: 1rem;\n            font-family: \"Gotham book\";\n            line-height: 150%;\n        }\n\n        a {\n            color: $col_lochmara;\n        }\n    }\n\n    &__inner {\n        .video